import React from 'react';

// NOTE: In a real Next.js/Shadcn project, you would ensure the Font Awesome
// library is imported globally (e.g., in _app.js or layout.js).
// Here, we use <i> tags with Font Awesome classes to simulate their usage.

// Define the content structure
const productLinks = [
    'AI Try-On',
    'Features',
    'How It Works',
    'Gallery',
    'Mobile App',
];

const companyLinks = [
    'About',
    'Us',
    'Careers',
    'Blog',
];

const supportLinks = [
    'Help Center',
    'Contact Us',
    'Privacy Policy',
    'Terms of Service',
];

// Helper Component for a single column of links
const FooterColumn = ({ title, links }) => (
    <div className="space-y-4">
        {/* Uses shadcn/ui typical text styling for headers */}
        <h3 className="text-lg font-semibold text-gray-900 tracking-tight">{title}</h3>
        <ul className="space-y-3">
            {links.map((link) => (
                <li key={link}>
                    {/* Link styling adheres to shadcn/ui subtle interaction design */}
                    <a
                        href="#"
                        className="text-gray-600 hover:text-gray-900 transition-colors duration-200 text-sm hover:text-primary"
                    >
                        {link}
                    </a>
                </li>
            ))}
        </ul>
    </div>
);

// Helper Component for Social Icons
const SocialIcon = ({ faClass, label }) => (
    <a
        href="#"
        aria-label={label}
        className="text-gray-600 hover:text-gray-900 transition-colors duration-200"
    >
        {/* Font Awesome 5/6 usage pattern */}
        <i className={`${faClass} w-5 h-5 text-xl`}></i>
    </a>
);


// Main Footer Component
const Footer = () => {
    return (
        // Custom background color to match the image's pale pink/lavender.
        // Using a light pink color defined via custom hex for accuracy: #fef8fb
        <footer className="bg-[#fef8fb] pt-16 pb-8 font-sans">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                {/* Main Footer Grid Section */}
                <div className="grid grid-cols-2 md:grid-cols-4 gap-12 lg:gap-16 xl:gap-24 pb-12">

                    {/* Column 1: Logo and Description */}
                    <div className="space-y-6 col-span-2 md:col-span-1">
                        {/* Logo Section */}
                        <div className="flex items-center space-x-2">
                            <div
                                className="w-8 h-8 flex items-center justify-center rounded-lg p-1"
                                style={{
                                    // Gradient background for the icon container: pink to purple
                                    backgroundImage: 'linear-gradient(to right, #ec4899, #8b5cf6)',
                                }}
                            >
                                {/* Font Awesome class for a pencil/edit icon for the logo */}
                                <i className="fa-solid fa-pencil text-white text-base"></i>
                            </div>
                            <span className="text-xl font-bold text-gray-900">Wearify</span>
                        </div>

                        <p className="text-sm text-gray-600 leading-relaxed max-w-xs">
                            Revolutionizing online fashion shopping with AI-powered virtual try-ons.
                        </p>

                        {/* Social Icons (using Font Awesome classes) */}
                        <div className="flex space-x-4">
                            <SocialIcon faClass="fa-brands fa-facebook-f" label="Facebook" />
                            <SocialIcon faClass="fa-brands fa-instagram" label="Instagram" />
                            <SocialIcon faClass="fa-brands fa-twitter" label="Twitter" />
                            <SocialIcon faClass="fa-brands fa-linkedin-in" label="LinkedIn" />
                        </div>
                    </div>

                    {/* Column 2: Product Links */}
                    <FooterColumn title="Product" links={productLinks} />

                    {/* Column 3: Company Links */}
                    <FooterColumn title="Company" links={companyLinks} />

                    {/* Column 4: Support Links */}
                    <FooterColumn title="Support" links={supportLinks} />
                </div>

                {/* Separator */}
                <div className="pt-8 border-t border-gray-200">

                    {/* Copyright Section */}
                    <p className="text-xs text-gray-600">
                        Â© 2025 Wearify. Virtual AI Try-On. All rights reserved.
                    </p>
                </div>
            </div>
        </footer>
    );
};

export default Footer;